* {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  cursor: pointer;
  user-select: none;
}

body {
  background-color: rgb(31, 31, 31);
}

.frame {
  position: absolute;
  border-radius: 5%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: aqua;
  align-items: center;
  height: 50%;
  aspect-ratio: 1 / 1;
  margin: auto;
  border: solid blue;
}

.header {
  color: blueviolet;
  font-size: 5vh;
}

.ticTacToeFrame {
  border: solid blue;
  background-color: blue;
  width: 50%;
  aspect-ratio: 1 / 1;

  display: flex;
  flex-flow: row wrap;
}

.gridBox {
  background-color: aqua;

  width: calc(94% / 3);
  aspect-ratio: 1 / 1;
  margin: 1%;

  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 9vh;
  font-family: Monospace monaco;
}